const app = () => {
  const popUp = document.querySelector("#pop-up");
  const select = document.querySelector("#select");
  const selected = document.querySelector("#selected");

  let isPopUpOpen = false;
  let isSelectOpen = false;

  const values = {
    name: "",
    email: "",
    occupacy: ""
  };

  const closePopUp = () => {
    popUp.classList.remove("pop-up__overlay_active");
    isPopUpOpen = false;
  };

  const openPopUp = () => {
    popUp.classList.add("pop-up__overlay_active");
    isPopUpOpen = true;
    window.addEventListener("keydown", (event) => {
      const { key } = event;

      if (key === "Escape" && isPopUpOpen) {
        closePopUp();
      }
    });
  };

  const preventClose = (event) => {
    event.stopPropagation();
  };

  const handleChange = (event) => {
    const { name, value } = event.target;

    values[name] = value;
  };

  const handleSelect = (event) => {
    const value = event.innerText;

    selected.innerText = value;
    selected.setAttribute("name", value);
    values.occupacy = value;

    if (!isSelectOpen) {
      select.classList.add("select_active");
      isSelectOpen = true;
    } else {
      select.classList.remove("select_active");
      isSelectOpen = false;
    }
  };
};

app();
